.pagination {
    width: 50%;
    text-align: center;
}
.pageNumber {
    padding: 0 8px;
}
.pageNumber:hover {
    cursor: pointer;
    color: white;
    font-style: italic;
    box-shadow: 2px 4px 5px #000;
    border-radius: 50%;
}
.selected {
    color: gold;
    font-weight: bold;
}
.ahead {
    padding: 0 2px;
    color: white;
    border-radius: 10%;
    background-color: #c7c785;
    margin: 0px 5px;
}
.ahead:hover {
    border-radius: 10%;
}